.chat-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    width: 100%;
    max-width: 600px;
    margin: auto;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .message-list {
    flex-grow: 1;
    overflow-y: auto;
    padding: 10px;
  }
  
  .message {
    margin: 5px 0;
    padding: 10px;
    border-radius: 5px;
    max-width: 70%;
  }
  
  .incoming {
    background-color: #e1f5fe;
    align-self: flex-start;
  }
  
  .outgoing {
    background-color: #c8e6c9;
    align-self: flex-end;
  }
  
  .input-container {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ddd;
    background-color: #fff;
  }
  
  .input-container input {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .input-container button {
    margin-left: 10px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #0288d1;
    color: white;
    cursor: pointer;
  }
  
  .input-container button:hover {
    background-color: #0277bd;
  }
  